
.md-sidenav .md-sidenav-content {
  background: linear-gradient(-50deg, #00142d, #002b60);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: start;
  justify-content: start;
}
.md-sidenav .md-sidenav-content ul.md-list {
    padding: 0;
    background: initial;
}
.md-sidenav .md-sidenav-content ul.md-list li {
      height: 86px;
}
.md-sidenav .md-sidenav-content ul.md-list li a.router-link-exact-active {
        box-shadow: 30px 0px #cbe2ff, 36px 0px #93C3FF;
        background: rgba(255, 255, 255, 0.1);
}
.md-sidenav .md-sidenav-content ul.md-list li a.md-list-item-container {
        -ms-flex-pack: center;
        justify-content: center;
        height: 100%;
}
.md-sidenav .md-sidenav-content ul.md-list li a.md-list-item-container span {
          font-size: 36px;
          color: white;
}
@media (min-width: 1280px) {
.md-sidenav .md-sidenav-content {
    top: 0 !important;
    width: 100px;
    margin-top: 63px;
    overflow: initial;
    pointer-events: auto !important;
    transform: translate3d(0, 0, 0) !important;
    box-shadow: 30px 0 #e5f0ff, 31px 0 rgba(160, 174, 191, 0.5);
}
}



