
.vuestic-tabs .simple-select {
  padding-top: 1.5rem;
}
.vuestic-tabs .nav {
  margin: 0;
}
.vuestic-tabs .nav .nav-item {
    text-align: center;
    padding: 1.5em 3em;
    background: rgba(255, 255, 255, 0.5);
    -ms-flex: 0 1;
        flex: 0 1;
}
.vuestic-tabs .nav .nav-item .nav-link {
      padding: 0;
      color: #adb3b9;
      transition: all .3s ease;
}
.vuestic-tabs .nav .nav-item .nav-link h5 {
        margin-bottom: 0;
        font-weight: 100;
}
.vuestic-tabs .nav .nav-item:hover {
      cursor: pointer;
}
.vuestic-tabs .nav .nav-item:hover .nav-link {
        color: #34495e;
}
.vuestic-tabs .nav .nav-item.active {
      background: white;
}
.vuestic-tabs .nav .nav-item.active .nav-link {
        color: #93C3FF;
}
.vuestic-tabs .nav .nav-item.active .nav-link h5 {
          font-weight: 500;
}
.vuestic-tabs .tab-content > .tab-pane {
  width: 100%;
}



