
.vuestic-scrollbar {
  background: transparent;
  transition: all .3s linear;
  position: relative;
}
.vuestic-scrollbar .scrollbar-wrapper {
    position: relative;
    max-height: 100%;
}
.vuestic-scrollbar .scrollbar-wrapper .track {
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 3px;
}
.vuestic-scrollbar .scrollbar-wrapper .track .thumb {
        transition: height .3s linear, opacity .6s linear;
        position: absolute;
        width: 100%;
        background-color: black;
        opacity: 0;
}
.vuestic-scrollbar .scrollbar-wrapper .track .thumb.active {
          opacity: .3;
}
.vuestic-scrollbar:hover .thumb.active {
    opacity: 1 !important;
}



