
.vuestic-progress-bar .horizontal {
  display: inline-block;
  width: 100%;
}
.vuestic-progress-bar .horizontal .progress-bar {
    transition: background-color ease .5s, width 3s linear !important;
}
.vuestic-progress-bar .horizontal .value {
    text-align: center;
}
.vuestic-progress-bar .horizontal .value.hidden {
      visibility: hidden;
}
.vuestic-progress-bar .horizontal .basic {
    border-radius: 0.5rem;
}
.vuestic-progress-bar .horizontal .basic .progress-bar {
      border-radius: inherit;
      height: 0.5rem;
}
.vuestic-progress-bar .horizontal .thin .progress-bar {
    height: 0.125rem;
}
.vuestic-progress-bar .horizontal .thick {
    border-radius: 1.5rem;
}
.vuestic-progress-bar .horizontal .thick .progress-bar {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      border-radius: inherit;
      height: 1.5rem;
}



