
.auth-layout {
  margin: 0;
  height: 100%;
  min-height: 100vh;
}
.auth-layout .nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 5.625rem;
    background-color: white;
}
.auth-layout .nav .i-vuestic {
      height: 1.5rem;
      width: 100%;
}
.auth-layout .main {
    margin: 0;
    height: 100%;
    min-height: 100vh;
}
.auth-layout .main > * {
      -ms-flex: 1 0 50%;
          flex: 1 0 50%;
}
.auth-layout .main .auth-content {
      min-height: 100vh;
      background: linear-gradient(45deg, #b9d8ff, #fff);
}
.auth-layout .main .auth-wallpaper {
      background: linear-gradient(135deg, #fff, #4d9cff);
}



