
#app-wrap {
  height: 100%;
}
#app-wrap #app-content {
    padding-top: 63px;
    padding-left: 1em;
    padding-right: 1em;
}
#app-wrap #app-content #content-outer-wrap #content-inner-wrap {
      transition: margin-left 0.3s ease;
}
#app-wrap #app-content #content-outer-wrap #content-inner-wrap #content {
        margin: 0 auto;
        max-width: 768px;
        padding-top: 2em;
}
#app-wrap #app-content #content-outer-wrap #content-inner-wrap #content .pre-loader {
          position: absolute;
          left: calc(50% - 140px/2);
          top: calc(50% - 104px/2);
}
@media (min-width: 768px) {
#content-outer-wrap {
    margin-left: 100px;
}
}



