
.icon-menu {
  font-size: 24px;
  color: #646EFF;
}
.logo {
  font-weight: 700;
  color: #90abcd;
  letter-spacing: 3px;
  margin-left: 15px;
}
#sep-sm {
  display: block;
}
.nav-user img {
  height: 40px;
  margin-left: 5px;
}
.nav-user h6 {
  margin: 0 0 0 1em;
}
.icon-refresh-cw {
  margin-right: 5px;
}
@media (min-width: 1280px) {
.icon-menu {
    display: none;
}
}



