
.vuestic-switch {
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.vuestic-switch .vuestic-switch-option {
    padding: 0.313rem 2.375rem;
    border-top: 0.125rem solid transparent;
    border-bottom: 0.125rem solid #93C3FF;
    background-color: transparent;
    transition: all 0.15s ease;
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.vuestic-switch .vuestic-switch-option.active {
      color: #fff;
      background-color: #93C3FF;
}
.vuestic-switch .vuestic-switch-option:not(.active):hover {
      color: #93C3FF;
}



