
.vuestic-progress-bar .circle.progress-bar {
  float: left;
  position: relative;
  width: 3.125rem;
  height: 3.125rem;
  padding-left: 0.125rem;
  padding-top: 0.125rem;
  border-radius: 50%;
  border-width: 0;
}
.vuestic-progress-bar .circle .overlay {
  width: calc(3.125rem - 2*0.125rem);
  height: 2.875rem;
  border-radius: 50%;
  border-width: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}



