
.vuestic-progress-bar .vertical .progress-bar {
  transition: background-color ease .5s, height 3s linear !important;
}
.vuestic-progress-bar .vertical .progress {
  height: 5.75rem;
  float: left;
  /* OLD - iOS 6-, Safari 3.1-6, BB7 */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  /* NEW - Safari 6.1+. iOS 7.1+, BB10 */
  display: flex;
  /* NEW, Spec - Firefox, Chrome, Opera */
  -ms-flex-align: start;
      align-items: flex-start;
  -webkit-align-items: flex-start;
  /* Safari 7.0+ */
}
.vuestic-progress-bar .vertical .value {
  float: left;
  height: 5.75rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: .25rem;
}
.vuestic-progress-bar .vertical .basic {
  border-radius: 0.5rem;
}
.vuestic-progress-bar .vertical .basic .progress-bar {
    border-radius: inherit;
    width: 0.5rem;
}
.vuestic-progress-bar .vertical .thin .progress-bar {
  width: 0.125rem;
}



