}
.vuestic-feed .post .underscored .text {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
}
.vuestic-feed .post .underscored .text .name {
          font-weight: 700;
}
.vuestic-feed .post .underscored .close-btn {
        margin-left: 1rem;
}
.vuestic-feed .post .underscored .close-btn i {
          left: 10.5px;
}
.vuestic-feed .post.last .underscored {
      border-bottom: none;
}



