
.widget {
  padding: 0;
}
.widget.no-header .widget-body {
    height: 100%;
}
.widget.larger-padding .widget-body {
    padding: 45px;
}
.widget.no-h-padding .widget-body {
    padding-left: 0;
    padding-right: 0;
}
.widget.no-v-padding .widget-body {
    padding-top: 0;
    padding-bottom: 0;
}
.widget.brand-info {
    background-color: #4ab2e3;
    color: #fff;
    box-shadow: 0px 4px 70px -16px #4ab2e3;
}
.widget.brand-danger {
    background-color: #e34a4a;
    color: #fff;
    box-shadow: 0px 4px 70px -16px #e34a4a;
}
.widget.info-widget {
    border-top: 0.5rem solid #93C3FF;
}
.widget.info-widget.brand-info {
      border-top-color: #4ab2e3;
}
.widget.info-widget.brand-danger {
      border-top-color: #e34a4a;
}
.widget .widget-body {
    height: calc(100% - 55px);
    padding: 1.5625rem;
}
.widget .widget-header {
    height: 55px;
    padding: 0 1.5625rem;
    border-bottom: 2px solid #fff;
    font-size: 1.2rem;
    font-weight: 600;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}



