
#intro-dialog #intro-content-wrap {
  width: 800px;
  max-width: 100%;
}
#intro-dialog button.md-button.md-theme-default:not(.md-primary):not(.md-icon-button) {
  background: white !important;
  color: #003374 !important;
}
#intro-dialog .md-whiteframe-1dp {
  box-shadow: initial;
}
#intro-dialog .step-content {
  padding: 0 2em 2em;
  max-width: 350px;
  margin: 0 auto;
}
#intro-dialog .md-step-content {
  text-align: center;
}



