.box {
  position: relative;
  margin-bottom: 2em;
  background: white;
  padding: 1em;
  box-shadow: 0px 10px 40px -5px #dae2eb;
  border-radius: 5px;
}
.box.inverse {
    color: white;
}
.box.inverse * {
      color: white;
}
.box.unpadded {
    padding: 0;
}
.box.lt {
    border: 1px solid;
    box-shadow: initial !important;
}
.box.warning * {
    color: #ff981b;
}
.box.ok {
    padding: 1em calc(2em + 10px) 1em 2em;
    box-shadow: 0px 10px 40px -5px #12e212;
    background: linear-gradient(135deg, #10E210, #019b01);
}
.box.ok .md-body-1 {
      color: white;
      margin: 0;
      font-weight: 300;
}
.box.ok button.md-button {
      background: white !important;
      color: #019b01 !important;
      margin: 0;
}
.box.ok.lt {
      border-color: #b8f0d1;
      background-color: #e3f9ed;
}
.box.ok.lt p {
        color: #12e212;
}
.box.warn {
    padding: 1em calc(2em + 10px) 1em 2em;
}
.box.warn .md-body-1 {
      margin: 0;
}
.box.warn.lt {
      border-color: #ffefc0;
      background-color: #fff8e6;
}
.box.warn.lt p {
        color: #dfb055;
}
.box.alert {
    padding: 1em calc(2em + 10px) 1em 2em;
    box-shadow: 0px 10px 40px -5px #de6a6a;
    background: #ff5050;
}
.box.alert .md-body-1 {
      color: white;
      margin: 0;
      font-weight: 300;
}
.box.alert.lt {
      border-color: #ffc8c8;
      background-color: #ffe9e9;
}
.box.alert.lt p {
        color: #de6a6a;
}
.box.alert button.md-button {
      background: white !important;
      color: #ff5050 !important;
      margin: 0;
}
.box:not(.alert):not(.ok):after:not(.warn) {
    position: absolute;
    top: 25px;
    left: -5px;
    right: 0;
    bottom: 0;
    content: '';
    height: 50px;
    width: 5px;
    background: #93C3FF;
    z-index: 1;
}
.box .md-table {
    margin: 0 -2em;
}
.box hgroup h1 {
    margin-bottom: .25em;
}
.box hgroup h1 i {
      line-height: 18px;
      margin-right: 15px;
      font-size: 20px;
      margin-left: 10px;
      color: #00142d;
      font-weight: 800;
